<template>
  <div id="toUnPayList">
    <div class="public_stylecss">
      <div class="clear wantMoney">
        <el-breadcrumb-item :to="{ path: '/index' }">
          <img class="back__" src="../../assets/images/back.png" alt="" style="width: 1rem;">
        </el-breadcrumb-item>
        <p class="fr union_head" @click="boxWarp">
          <img src="../../assets/images/switchover.png" alt="">
          <span class="unionName"></span>
        </p>
      </div>
      <div class="union_second payed">
        <p>{{totalMoney | filtration}}</p>
        <span>()</span>
      </div>
    </div>
    <p class="record"></p>
    <div class="footer_Css">
      <ul >
        <li class="clear" v-for="(item, index) in unCommission">
          <div class="clear fl">
            <div class="fl">
              <p>{{item.union.name}}</p>
              <span style="color: #7e7e7e">{{item.opportunity.createTime}}</span>
            </div>
            <img src="../../assets/images/urge.png" @click="showModel_">
            <div class="box-wrap2" style="display: none">
              <div class="mask" @click="hide1_"></div>
              <div class="box">
                <p>?</p>
                <div class="fr" @click="send1_(item.opportunity.id)"></div>
                <div style="border-left:none" @click="hide1_"></div>
              </div>
            </div>
          </div>
          <div class="clearfr">
            <div class="fr">
              <span>{{item.toMember.enterpriseName}}</span>
              <p class="" style="color: #5EB6FF;text-align: right;">{{item.opportunity.money}}</p>
            </div>
          </div>
        </li>
      </ul>
      <div class="loadMore hasPayLoadMore" @click="loadMore" ></div>
      <div class="nothing hasPayNothing" style="color:#868686;" ></div>
    </div>
    <!---->
    <div class="box-wrap" style="display: none">
      <div class="mask" @click="hide_"></div>
      <div class="box">
        <div id="triangle-up"></div>
        <ul>
          <li  @click="fullyLoaded"></li>
          <li v-for="(item, index) in unionList" @click="partLoaded(item.id,item.name)">{{item.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import $http from '@/utils/http.js'
  import $todate from '@/utils/todate.js'
  export default {
    name: 'toUnPayList',
    data() {
      return {
//        
        toLogin: 'ceshi1',
        //
        unCommission:[],
        //
        unionList:[],
        //
        totalMoney:0,
        //
        size:6,
        //
        current:1,
        //id
        unionId:'',
      }
    },
    methods: {
      boxWarp(){
        ///
        $('.box-wrap').show();
      },
      hide_(){
        $('.box-wrap').hide();
      },
      showModel_(){
        $('.box-wrap2').show();
      },
      hide1_(){
        $('.box-wrap2').hide();
      },
      //
      send1_(did){
        $('.box-wrap2').hide();
        $http.post(`/h5Brokerage/unreceived/urge?opportunityId=${did}`)
          .then(res => {
            console.log(res.data);
            if(res.data.success){
                this.$message({
                  showClose: true,
                  message: "",
                  type: 'success',
                  duration: 3500
                });
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
      },
      //
      fullyLoaded(){
        document.querySelector('.unionName').innerHTML='';
        //
        let data={
          size:this.size=6,
          current:this.current=1
        }
        $http.get(`/h5Brokerage/unreceived/page`,data)
          .then(res => {
            if(res.data.data) {
              if (res.data.data.records.length === 0) {
                $('.hasPayNothing').show();
                $('.hasPayLoadMore').hide();
                this.unCommission = [];
                this, totalMoney = 0;
              }
              else if (res.data.data.records.length < this.size) {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore').hide();
              } else {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore').show();
              }
              this.unCommission = res.data.data.records;
              this.unCommission.forEach((v, i) => {
                v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
              })
              //
              $http.get(`/h5Brokerage/unreceived/sum`)
                .then(res => {
                  if(res.data.data) {
                    this.totalMoney = res.data.data;
                  }
                })
                .catch(err => {
                  this.$message({showClose: true, message: err.toString(), type: 'error', duration: 3000});
                });
              $('.box-wrap').hide();
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
      },
      //
      partLoaded(did,uname){
        $('.box-wrap').hide();
        //id
        this.unionId=did;
        document.querySelector('.unionName').innerHTML=uname;
        //id
        let data1={
          size:this.size=6,
          current:this.current=1,
          unionId:did
        };
        $http.get(`/h5Brokerage/unreceived/page`,data1)
          .then(res => {
            if(res.data.data) {
              if (res.data.data.records.length === 0) {
                this.totalMoney = 0;
                this.unCommission = [];
                $('.hasPayNothing').show();
                $('.hasPayLoadMore').hide();
                return;
              } else if (res.data.data.records.length < this.size) {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore').hide();
              }
              else {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore').show();
              }
              this.unCommission = res.data.data.records;
              this.unCommission.forEach((v, i) => {
                v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
              })
              //id
              $http.get(`/h5Brokerage/unreceived/sum`,{unionId:did})
                .then(res => {
                  if(res.data.data) {
                    this.totalMoney = res.data.data;
                  }
                })
                .catch(err => {
                  this.$message({showClose: true, message: err.toString(), type: 'error', duration: 3000});
                });
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
      },
      //
      loadMore(){
        console.log(++this.current);
        let data1={
          size:this.size=6,
          current:this.current,
          unionId:this.unionId
        }
        $http.get(`/h5Brokerage/unreceived/page`,data1)
          .then(res => {
            if(res.data.data) {
              let list = res.data.data.records;
              if (list.length < this.size) {
                $('.hasPayNothing').show();
                $('.hasPayLoadMore').hide();
              } else {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore').show();
              }
              this.unCommission = this.unCommission.concat(list);
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
      },
    },
  //
    filters:{
      filtration:function(value){
        return value = value.toFixed(2);
      }
    },
    created (){
      //title
      $("#title_").text('');
      //
      this.$emit('getValue',this.toLogin);
      //---------------------------------------------------------------1
      let data={
        size:this.size,
        current:this.current
      }
      $http.get(`/h5Brokerage/unreceived/page`,data)
        .then(res => {
          if(res.data.data) {
            if (res.data.data.records.length === 0) {
              this.totalMoney = 0;
              this.unCommission = [];
              $('.hasPayNothing').show();
              $('.hasPayLoadMore').hide();
            } else if (res.data.data.records.length < this.size) {
              $('.hasPayNothing').hide();
              $('.hasPayLoadMore').hide();
            }
            else {
              $('.hasPayNothing').hide();
              $('.hasPayLoadMore').show();
            }
            this.unCommission = res.data.data.records;
            this.unCommission.forEach((v, i) => {
              v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
            })
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
        });
      //----------------------------------------------------------------2
      $http.get(`/h5Brokerage/myUnion`)
        .then(res => {
          if(res.data.data) {
            this.unionList = res.data.data;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
        });
      //----------------------------------------------------------------3
      $http.get(`/h5Brokerage/unreceived/sum`)
        .then(res => {
          if(res.data.data) {
            this.totalMoney = res.data.data;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
        });
    }
  }
</script>
<style lang='less' rel="stylesheet/less" scoped>
  .hasPayLoadMore,.hasPayNothing{
    font-size: 0.85rem;
    color: #39ACFF;
    text-align: center;
    padding: 0.3rem;
  }
</style>



