<template>
  <div id="Index0" class="main">
    <div class="main-title">
      <p>{{dataList[0]}}</p>
      <span></span>
    </div>
    <div class="flex main-wrap">
      <div class="flex-1">
          <i class="fr tubiao"></i>
          <strong></strong>
          <p>{{dataList[1]}}</p>
      </div>
      <div class="flex-1">
            <strong></strong>
            <p>{{dataList[2]}}</p>
      </div>
    </div>
    <router-link :to="{ path:'/toExtract',name:'toExtract'}">
        <div class="main_list clear">
            <img src="../../assets/images/widthDraw-1.png" class="fl">
            <div class="fl">
              <h2></h2>
              <p></p>
            </div>
        </div>
    </router-link>
    <router-link :to="{ path:'/toPayList',name:'toPayList'}">
      <div class="main_list clear">
          <img src="../../assets/images/widthDraw-3.png" class="fl">
          <div class="fl">
            <h2></h2>
            <p></p>
          </div>
      </div>
    </router-link>
    <router-link :to="{ path: '/toUnPayList', name: 'toUnPayList'}">
      <div class="main_list clear">
          <img src="../../assets/images/widthDraw-4.png" class="fl">
          <div class="fl">
            <h2></h2>
            <p></p>
          </div>
      </div>
    </router-link>
  </div>
</template>

<script>
  import $http from '@/utils/http.js'
  export default {
    name: 'index',
    data() {
      return {
//        
        toLogin: 'ceshi1',
        dataList:[],
        canReflect:'',
        unpaid:'',
        nonPayment:'',
      }
    },
    methods: {

    },
//    
    created (){
      $("#title_").text('');
      //
      this.$emit('getValue',this.toLogin);
      //
      $http.get("/h5Brokerage/index")
        .then(res => {
          if(res.data.data) {
            for (var key in res.data.data) {
              this.dataList.push(res.data.data[key].toFixed(2));
            }
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
        });
    }
  }
</script>
<style lang='less' rel="stylesheet/less">

</style>



