<template>
  <div id="toPayList" >
    <!---->
    <div class="public_stylecss">
      <div class="clear wantMoney">
        <el-breadcrumb-item :to="{ path: '/index' }">
          <img class="back__" src="../../assets/images/back.png" alt="" style="width: 1rem;">
        </el-breadcrumb-item>
        <p class="fr union_head" @click="boxWarp">
          <img src="../../assets/images/switchover.png" alt="">
          <span  class="unionName"></span>
        </p>
      </div>
      <div class="union_second payed passive">
        <p>{{payMoney | filtration}}</p>
        <span>()</span>
      </div>
      <!-- passive -->
      <div class="union_second  unpay clear">
        <div class="fl">
          <p>{{totalMoney | filtration}}</p>
          <span>()</span>
        </div>
        <div class="fr payment" @click="showModel3_">
          
        </div>
        <div class="box-wrap3" style="display: none">
          <div class="mask" @click="hide3_"></div>
          <div class="box">
            <p>{{totalMoney}}?</p>
            <div class="fr" @click="send3_()"></div>
            <div style="border-left:none" @click="hide3_"></div>
          </div>
        </div>
      </div>
    </div>
    <!---->
    <div class=" detail-wrap clear">
      <div class='payTab flex flex-pack-center'>
        <div class="focusPay"></div>
        <div class=""></div>
      </div>
      <p class="record"><p>
      <!---->
      <div class="unpay ">
        <ul >
          <li class="clear" v-for="(item1, index) in unPayList">
            <div class="clear fl">
              <div class="fl clear">
                <div class='fl'>
                  <p class="">{{item1.union.name}}</p>
                  <span style="color: #7e7e7e">{{item1.opportunity.createTime}}</span>
                </div>
                <div class='fl'>
                  <el-button class='goPay'  @click="showModel_(index)"></el-button>
                </div>
                <div class="box-wrap2" style="display: none">
                  <div class="mask" @click="hide1_"></div>
                  <div class="box">
                    <p>{{item1.opportunity.brokerageMoney}}?</p>
                    <div class="fr" @click="send1_(item1.opportunity.id,item1.union.id)"></div>
                    <div style="border-left:none" @click="hide1_"></div>
                  </div>
                </div>
              </div>
            </div>
            <div class="clear fr">
              <div class="fr">
                <span>{{item1.fromMember.enterpriseName}}</span>
                <p class="" style="color: #90e88f;text-align: right;">-{{item1.opportunity.brokerageMoney | filtration}}</p>
              </div>
            </div>
          </li>
        </ul>
        <div class="loadMore hasPayLoadMore1"  @click="loadMore1"></div>
        <div class="nothing noPayNothing" style="display: none;color:#868686;"></div>
      </div>
      <!---->
      <div class="payed passive">
        <ul >
          <li class="clear" v-for="(item, index) in payList">
            <div class="clear fl">
              <div class='fl'>
                <p>
                  {{item.union.name}}
                </p>
                <span style="color: #7e7e7e">
                      {{item.opportunity.createTime}}
                    </span>
              </div>
            </div>
            <div class="clearfr">
              <div class="fr">
                <span>{{item.fromMember.enterpriseName}}</span>
                <p class="" style="color: #90e88f;text-align: right;">{{item.opportunity.brokerageMoney | filtration}}</p>
              </div>
            </div>
          </li>
        </ul>
        <div class="loadMore hasPayLoadMore2"  @click="loadMore2"></div>
        <div class="nothing hasPayNothing" style="display: none;color:#868686;"></div>
      </div>
    </div>
    <!---->
    <div class="box-wrap" style="display: none">
      <div class="mask" @click="hide_"></div>
      <div class="box">
        <div id="triangle-up"></div>
        <ul>
          <li  @click="fullyLoaded"></li>
          <li v-for="(item, index) in unionList" @click="partLoaded(item.id,item.name)">{{item.name}}</li>
        </ul>
      </div>
    </div>
  </div>

</template>

<script>
  import $http from '@/utils/http.js'
  import $todate from '@/utils/todate.js'
  export default {
    name: 'toPayList',
    data() {
      return {
//        
        toLogin: 'ceshi1',
        //
        unPayList:[],
        //
        payList:[],
        //
        unionList:[],
        //
        totalMoney:0,
        //
        payMoney:0,
        //
        size:6,
        //
        current1:1,
        current2:1,
        //id
        unionId:'',
        //
        totalMoney12:0
      }
    },
    mounted(){
      //
      $('.payTab div').on('touchend', function(){
        let index = $(this).index();
        $(this).addClass('focusPay').siblings('.payTab div').removeClass('focusPay');
        if(index == 1){//
          if($(".payed").hasClass("passive")){
            $(".payed").removeClass("passive");
            $(".unpay").addClass("passive");
          }
        }else if(index == 0){//
          if($(".unpay").hasClass("passive")){
            $(".payed").addClass("passive");
            $(".unpay").removeClass("passive");
          }
        }
      });
    },
    //
    filters:{
      filtration:function(value){
        return value = value.toFixed(2);
      }
    },
    methods: {
      //
      boxWarp(){
          $('.box-wrap').show();
      },
      //
      hide_(){
        $('.box-wrap').hide();
      },
      //
      showModel_(index){
        $($('.box-wrap2')[index]).show();
      },
      hide1_(){
        $('.box-wrap2').hide();
      },
      //id
      send1_(oid,uid){
        $('.box-wrap2').hide();
//        
//        let url='toPayList';
        $http.put(`/h5Brokerage/pay/unpaid/toPay?unionId=${uid}&opportunityId=${oid}`)
          .then(res => {
            if(res.data.success) {
              //
              location.href = "/#/toPayList";
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
      },
      //
      showModel3_(){
        if(this.totalMoney>0) {
          $('.box-wrap3').show();
        }else{
          this.$message({ showClose: true, message: '', type: 'error', duration: 3000 });
        }
      },
      //
      hide3_(){
        $('.box-wrap3').hide();
      },
      //
      send3_(){
        let uid=this.unionId;
        //
        $http.put(`/h5Brokerage/pay/unpaid/batchPay?unionId=${uid}`)
          .then(res => {
            if(res.data.success) {
              location.href = "/#/toPayList";
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
//        let url='toPayList';
//        $http.post(`/unionH5Brokerage/allPay?unionId=${uid}&fee=${this.totalMoney12}&url=${url}`)
//          .then(res => {
//            if(res.data.data) {
//              //
//              location.href = res.data.data;
//              //
////            history.go(0)
//            }
//          })
//          .catch(err => {
//            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
//          });
        $('.box-wrap3').hide();
      },
//    ''
      fullyLoaded(){
        //
        //Id
        this.unionId='';
        document.querySelector('.unionName').innerHTML='';
        let data={
          size:this.size=6,
          current:this.current1
        }
        //-------------1
        $http.get(`/h5Brokerage/pay/unPaid/page`,data)
          .then(res => {
            if(res.data.data) {
              if (res.data.data.records.length === 0) {
                $('.noPayNothing').show();
                $('.hasPayLoadMore1').hide();
                this.totalMoney = 0;
                this.unPayList = [];
              } else if (res.data.data.records.length < this.size) {
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').hide();
              }
              else {
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').show();
              }
              //
              this.unPayList = res.data.data.records;
              this.unPayList.forEach((v, i) => {
              v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
            })
              //
              $http.get(`/h5Brokerage/pay/unPaid/sum`)
                .then(res => {
                  if(res.data.data){
                    this.totalMoney = res.data.data;
                  }
                })
                .catch(err => {
                  this.$message({showClose: true, message: err.toString(), type: 'error', duration: 3000});
                });
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
        //-------------2
          let data1={
            size:this.size,
            current:this.current2=1
          }
          $http.get(`/h5Brokerage/pay/paid/page`,data1)
            .then(res => {
              if(res.data.data) {
                if (res.data.data.records.length === 0) {
                  this.payMoney = 0;
                  this.payList = [];
                  $('.hasPayNothing').show();
                  $('.hasPayLoadMore2').hide();
                } else if (res.data.data.records.length < this.size) {
                  $('.hasPayNothing').hide();
                  $('.hasPayLoadMore2').hide();
                }
                else {
                  $('.hasPayNothing').hide();
                  $('.hasPayLoadMore2').show();
                }
                //
                this.payList = res.data.data.records;
                this.payList.forEach((v, i) => {
                  v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
                })
                //
                $http.get(`/h5Brokerage/pay/paid/sum`)
                  .then(res => {
                    if(res.data.data) {
                      this.payMoney = res.data.data;
                    }
                  })
                  .catch(err => {
                    this.$message({showClose: true, message: err.toString(), type: 'error', duration: 3000});
                  });
              }
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
            });
        $('.box-wrap').hide();
      },
//      '';
      partLoaded(did,uname){
        let data1={
          size:this.size=6,
          current:this.current1=1,
          unionId:did
        };
        //id
        this.unionId=did;
        document.querySelector('.unionName').innerHTML=uname;
        //-----------------1
        $http.get(`/h5Brokerage/pay/paid/page`,data1)
          .then(res => {
            if(res.data.data) {
              if (res.data.data.records.length === 0) {
                $('.noPayNothing').show();
                $('.hasPayLoadMore1').hide();
                this.totalMoney = 0;
                this.unPayList = [];
              } else if (res.data.data.records.length < this.size) {
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').hide();
              }
              else {
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').show();
              }
              //
              this.unPayList = res.data.data.records;
              this.unPayList.forEach((v, i) => {
                v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
              })
              //
              $http.get(`/h5Brokerage/pay/unPaid/sum`,{unionId:did})
                .then(res => {
                  if(res.data.data) {
                    this.totalMoney = res.data.data;
                  }
                })
                .catch(err => {
                  this.$message({showClose: true, message: err.toString(), type: 'error', duration: 3000});
                });
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
        //----------------2
        let data2={
          size:this.size=6,
          current:this.current2=1,
          unionId:did
        };
        $http.get(`/h5Brokerage/pay/paid/page`,data2)
          .then(res=>{
            if(res.data.data) {
              if (res.data.data.records.length === 0) {
                this.payMoney = 0;
                this.payList = [];
                $('.hasPayNothing').show();
                $('.hasPayLoadMore2').hide();
              } else if (res.data.data.records.length < this.size) {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore2').hide();
              }
              else {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore2').show();
              }
              //
              this.payList = res.data.data.records;
              this.payList.forEach((v, i) => {
                v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
              })
              //
              $http.get(`/h5Brokerage/pay/paid/sum`,{unionId:did})
                .then(res => {
                  if(res.data.data) {
                    this.payMoney = res.data.data;
                  }
                })
                .catch(err => {
                  this.$message({showClose: true, message: err.toString(), type: 'error', duration: 3000});
                });
            }
          }
        )
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
        });
        //
        $('.box-wrap').hide();
      },
      //
      loadMore1(){
        console.log(++this.current1);
        let data1={
          size:this.size=6,
          current:this.current1,
          unionId:this.unionId
        }
        $http.get(`/h5Brokerage/withdrawal/detail/opportunity/page`,data1)
          .then(res => {
            if(res.data.data) {
              let list = res.data.data.records;
              if (list.length < this.size) {
                $('.noPayNothing').show();
                $('.hasPayLoadMore1').hide();
              } else {
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').show();
              }
              //
              list.forEach((v, i) => {
                v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
              })
              this.unPayList = this.unPayList.concat(list);
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
      },
      //
      loadMore2(){
        console.log(++this.current2);
        let data1={
          size:this.size=6,
          current:this.current2,
          unionId:this.unionId
        }
        $http.get(`/h5Brokerage/pay/paid/page`,data1)
          .then(res => {
            if(res.data.data) {
              let list = res.data.data.records;
              if (list.length < this.size) {
                $('.hasPayNothing').show();
                $('.hasPayLoadMore2').hide();
              } else {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore2').show();
              }
              //
              list.forEach((v, i) => {
                v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
              })
              this.payList = this.payList.concat(list);
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          });
      },
    },
    created (){
      //title
      $("#title_").text('');
      //
      this.$emit('getValue',this.toLogin);
//--------------------------------------------------------------------------1
//      
      let data={
        size:this.size,
        current:this.current1
      };
      $http.get(`/h5Brokerage/pay/unPaid/page`,data)
        .then(res => {
          if(res.data.data) {
            if (res.data.data.records.length === 0) {
              $('.noPayNothing').show();
              $('.hasPayLoadMore1').hide();
              this.totalMoney = 0;
              this.unPayList = [];
            } else if (res.data.data.records.length < this.size) {
              $('.noPayNothing').hide();
              $('.hasPayLoadMore1').hide();
            }
            else {
              $('.noPayNothing').hide();
              $('.hasPayLoadMore1').show();
            }
            //
            this.unPayList = res.data.data.records;
            this.unPayList.forEach((v, i) => {
              v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
            })
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
        });
      //
      $http.get(`/h5Brokerage/pay/unPaid/sum`)
        .then(res => {
          if(res.data.data) {
            this.totalMoney = res.data.data;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
        });
// ------------------------------------------------------------------------------2
      //
      let data1={
        size:this.size,
        current:this.current2
      };
      $http.get(`/h5Brokerage/pay/paid/page`,data1)
        .then(res => {
          if(res.data.data) {
            if (res.data.data.records.length === 0) {
              this.payMoney = 0;
              this.payList = [];
              $('.hasPayNothing').show();
              $('.hasPayLoadMore2').hide();
            } else if (res.data.data.records.length < this.size) {
              $('.hasPayNothing').hide();
              $('.hasPayLoadMore2').hide();
            }
            else {
              $('.hasPayNothing').hide();
              $('.hasPayLoadMore2').show();
            }
            //
            this.payList = res.data.data.records;
            this.payList.forEach((v, i) => {
              v.opportunity.createTime = $todate.todate(new Date(v.opportunity.createTime));
            })
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
          console.log(666)
        });
      //
      $http.get(`/h5Brokerage/pay/paid/sum`)
        .then(res => {
          if(res.data.data) {
            this.payMoney = res.data.data;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
        });
//-----------------------------------------------------------------------3
      $http.get(`/h5Brokerage/myUnion`)
        .then(res => {
          if(res.data.data) {
            this.unionList = res.data.data;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 3000 });
        });
    }
  }
</script>
<style lang='less' rel="stylesheet/less" scoped>
.hasPayLoadMore1,.hasPayLoadMore2{
  font-size: 0.85rem;
  color: #39ACFF;
  text-align: center;
  padding: 0.3rem;
}
.nothing{
  text-align: center;
  padding: 0.3rem;
  font-size: 0.85rem;
}
</style>



